:root {
  --neutral-base-color: #ffffff;
  --bg-color: #000000;
  --sidebar-color: #000000;

  --icon-color: #ffffff;
  --icon-shadow-color: #ddddddae;

  --text-color: #ffffff;
  --input-text-color: #ffffff;

  --button-bg: #000000;
  --button-color: #ffffff;

  --button-border-color: #ffffff;
  --border-radius: 5px;
  --button-thickness: 1px;
  --button-font-weight: 1000;
  --button-letter-spacing: 1px;
  --button-padding: 5px 5px;
  --button-font-size: clamp(1.2rem, 2.5vw, 1.8rem);
  --button-width: auto;

  --input-highlight-color: #ffffff;
}

.light-theme {
  --neutral-base-color: #000000;
  --bg-color: #f5f5f5;
  --sidebar-color: #ffffff;
  --icon-color-active: #000000;
  --input-text-color: #000000;

  --icon-color: #000000;
  --icon-shadow-color: #000000ae;

  --text-color: #000000;

  --button-bg: #ffffff;
  --button-color: #000000;

  --button-border-color: #000000;
  --border-radius: 5px;
  --button-thickness: 1px;

  --input-highlight-color: #000000;
}

@import 'animate.css';

@font-face {
  font-family: 'Montserrat';
  src: url('./assets/fonts/Montserrat-Regular.ttf') format('truetype');
}

body {
  font-family: 'Montserrat';
  font-size: 50px;
  background: var(--bg-color);
  color: var(--text-color);
}

input,
textarea {
  font-family: 'Montserrat';
  border: none;
  color: var(--input-text-color);
  border-radius: var(--border-radius);
  background: color-mix(
    in srgb,
    var(--bg-color) 90%,
    var(--neutral-base-color) 20%
  );
}

input:focus {
  outline: none;
  border: 2px solid var(--input-highlight-color);
}

button,
.flat-button {
  background: var(--button-bg);
  color: var(--button-color);
  border: var(--button-thickness) solid var(--button-color);
  border-radius: var(--border-radius);
  font-size: var(--button-font-size);
  letter-spacing: var(--button-letter-spacing);
  flex: 0 0 auto;
  cursor: pointer;
  font-weight: var(--button-font-weight);
  letter-spacing: var(--button-letter-spacing);
  padding: var(--button-padding);
}

button:hover,
.flat-button:hover {
  background: var(--button-color);
  color: var(--button-bg);
  transition: all 0.5s ease-out;
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

a {
  color: var(--text-color);
}

p {
  font-size: clamp(1.2rem, 1.5vw, 2rem);
}

h1 {
  font-size: clamp(1.5rem, 2vw, 4rem);
  margin: 0;
}

.icon,
a {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

svg {
  color: var(--icon-color);
  font-size: 23px;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}
